<template>
  <div class="order-details-page" v-if="order">
    <div class="order-details-page__button">
      <vue-yellow-button-icon
        :title="'Назад'"
        :icon="icon_back"
        @click="goToBack"
        class="component_yellow_button_icon"
      />
    </div>
    <div class="order-details-page__title">
      <vue-title-text :title-params="h1Params" />
    </div>
    <div class="order-details-page__details">
      <div class="order-details-page__details__title">
        <h2>Задача: {{ order.title }}</h2>
      </div>
      <div class="order-details-page__details__description">
        <h3>Описание:</h3>
        <p>{{ order.description }}</p>
      </div>
      <div class="order-details-page__details__user">
        <p>
          Заказчик: <span>{{ order.customer }}</span>
        </p>
      </div>
      <div class="order-details-page__details__address">
        Адрес: <span>{{ order.location }}</span>
      </div>
      <div class="order-details-page__details__time">
        Начало работы в <span>{{ order.time }}</span>
      </div>
      <div class="order-details-page__details__worker-time">
        <p>
          Заказ рассчитан на <span>{{ order.workingHours }}мин.</span>
        </p>
      </div>
      <div class="order-details-page__details__taxi">
        <p v-if="order.taxi">{{ order.customer }} готов оплатить такси</p>
        <p v-else>{{ order.customer }} не готов оплатить такси</p>
      </div>
      <div class="order-details-page__details__payment-info">
        <vue-yellow-button
          class="component_yellow_button"
          :title="'Взять заказ'"
          @click="goToRegistration"
        />
        <p>
          Цена: <span>{{ order.price }}руб.</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useRoute, useRouter } from "vue-router";
import cardsData from "@/localDatabase/cards.json";
import { onMounted, ref } from "vue";
import VueTitleText from "@/components/Titles/VueTitleText.vue";
import VueYellowButtonIcon from "@/components/Buttons/VueYellowButtonIcon.vue";
import VueYellowButton from "@/components/Buttons/VueYellowButton.vue";
import icon_back from "@/assets/icons/back-arrow.svg";

const route = useRoute();
const router = useRouter();

let order = ref(null);

onMounted(async () => {
  const orderId = await Number(route.params.id);

  order.value = cardsData.find((item) => {
    return item.id === orderId;
  });
});
const h1Params = {
  title: "Детали заказа",
  tag: "h1",
  size: 80,
  dimension: "px",
};

const goToBack = () => {
  router.back(-1);
};
const goToRegistration = () => {
  window.location.replace("https://arbait-registration.vercel.app/");
};
</script>

